"use client";

import React, { useState } from "react";
import Link from "next/link";

import {
  DocumentMagnifyingGlassIcon,
  CloudArrowUpIcon,
  ArrowPathIcon,
  ArrowRightIcon,
  ExclamationTriangleIcon,
  ClockIcon,
  ChartBarIcon,
  DocumentTextIcon,
  AdjustmentsHorizontalIcon,
} from "@heroicons/react/24/outline";

type CompareResult = {
  doc_name: string;
  v1_label: string;
  v2_label: string;
  pages_v1: number;
  pages_v2: number;
  paragraphs_v1: number;
  paragraphs_v2: number;
  changes_count: number;
  risk_level: string;
  summary_text: string;
  json_report_path: string;
  html_report_path: string;
  run_id: number;
  html_report_url: string;
  json_report_url: string;
};

type ChangeType = "ADDED" | "REMOVED" | "MODIFIED";

type ChangeItem = {
  change_type: ChangeType;
  section_label: string | null;
  old_text: string | null;
  new_text: string | null;
  risk_level?: "LOW" | "MEDIUM" | "HIGH" | null;
  ai_comment?: string | null;
};

type JsonReport = {
  document_name: string;
  version_old: string;
  version_new: string;
  overall_risk_level: string | null;
  summary_text: string | null;
  changes: ChangeItem[];
};

export default function Home() {
  const [docName, setDocName] = useState("TestDoc");
  const [v1Label, setV1Label] = useState("v1");
  const [v2Label, setV2Label] = useState("v2");
  const [fileV1, setFileV1] = useState<File | null>(null);
  const [fileV2, setFileV2] = useState<File | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [result, setResult] = useState<CompareResult | null>(null);
  const [changes, setChanges] = useState<ChangeItem[]>([]);
  const [filterType, setFilterType] = useState<"ALL" | ChangeType>("ALL");

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setError(null);
    setResult(null);
    setChanges([]);

    if (!fileV1 || !fileV2) {
      setError("กรุณาเลือกไฟล์ PDF ทั้งสองเวอร์ชันก่อนเริ่มเปรียบเทียบ");
      return;
    }

    const formData = new FormData();
    formData.append("doc_name", docName);
    formData.append("v1_label", v1Label);
    formData.append("v2_label", v2Label);
    formData.append("file_v1", fileV1);
    formData.append("file_v2", fileV2);

    setLoading(true);
    try {
      const res = await fetch("http://127.0.0.1:8000/compare", {
        method: "POST",
        body: formData,
      });

      if (!res.ok) {
        const data = await res.json().catch(() => ({}));
        throw new Error(
          data.error || `เรียก API ไม่สำเร็จ (status ${res.status})`
        );
      }

      const data: CompareResult = await res.json();
      setResult(data);

      try {
        const jsonRes = await fetch(
          `http://127.0.0.1:8000${data.json_report_url}`
        );
        if (jsonRes.ok) {
          const json: JsonReport = await jsonRes.json();
          setChanges(json.changes || []);
        }
      } catch {}
    } catch (err: any) {
      setError(err.message || "เกิดข้อผิดพลาดไม่ทราบสาเหตุ");
    } finally {
      setLoading(false);
    }
  };

  const riskColor = (risk: string) => {
    const level = risk.toLowerCase();
    if (level.includes("high") || level.includes("สูง"))
      return "bg-red-100 text-red-700 border-red-200";
    if (level.includes("medium") || level.includes("กลาง"))
      return "bg-amber-100 text-amber-700 border-amber-200";
    return "bg-emerald-100 text-emerald-700 border-emerald-200";
  };

  const riskBadgeForChange = (risk?: string | null) => {
    const base =
      "inline-flex items-center px-2 py-0.5 rounded-full text-[11px] font-semibold border";
    const level = (risk || "LOW").toUpperCase();
    if (level === "HIGH")
      return (
        <span className={`${base} bg-red-100 text-red-700 border-red-200`}>
          HIGH
        </span>
      );
    if (level === "MEDIUM")
      return (
        <span className={`${base} bg-amber-100 text-amber-700 border-amber-200`}>
          MEDIUM
        </span>
      );
    return (
      <span className={`${base} bg-emerald-100 text-emerald-700 border-emerald-200`}>
        LOW
      </span>
    );
  };

  const filteredChanges =
    filterType === "ALL"
      ? changes
      : changes.filter((c) => c.change_type === filterType);

  const renderChangeBadge = (t: ChangeType) => {
    const base =
      "inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-[11px] font-semibold";
    if (t === "ADDED")
      return (
        <span className={`${base} bg-emerald-100 text-emerald-700`}>
          ＋ ADDED
        </span>
      );
    if (t === "REMOVED")
      return (
        <span className={`${base} bg-rose-100 text-rose-700`}>
          − REMOVED
        </span>
      );
    return (
      <span className={`${base} bg-amber-100 text-amber-700`}>
        <AdjustmentsHorizontalIcon className="h-3 w-3" />
        MODIFIED
      </span>
    );
  };

  const truncate = (text: string | null, len = 260) =>
    !text ? "" : text.length > len ? text.slice(0, len) + "..." : text;

  return (
    <main className="min-h-screen bg-gradient-to-br from-[#f6e9ff] via-[#f7f0ff] to-[#e3d4ff] px-4 py-8 flex items-center justify-center">
      <div className="w-full max-w-6xl">
        <div className="rounded-3xl bg-white/85 shadow-2xl border border-white/70 overflow-hidden">
          <div className="h-2 bg-gradient-to-r from-violet-500 via-fuchsia-400 to-pink-400" />

          <div className="grid md:grid-cols-[1.3fr_1fr] gap-8 p-6 md:p-10">
            {/* LEFT */}
            <section className="space-y-6">
              <h1 className="text-3xl font-bold text-slate-900">
                เปรียบเทียบเอกสาร PDF
              </h1>

              <form onSubmit={handleSubmit} className="space-y-5">
                <input
                  value={docName}
                  onChange={(e) => setDocName(e.target.value)}
                  className="w-full rounded-xl border border-slate-200 px-3 py-2 text-sm text-slate-800 focus:ring-2 focus:ring-violet-400"
                />

                <div className="grid md:grid-cols-2 gap-4">
                  <input
                    type="file"
                    accept="application/pdf"
                    onChange={(e) => setFileV1(e.target.files?.[0] || null)}
                  />
                  <input
                    type="file"
                    accept="application/pdf"
                    onChange={(e) => setFileV2(e.target.files?.[0] || null)}
                  />
                </div>

                {error && (
                  <div className="rounded-xl bg-red-50 border border-red-200 px-3 py-2 text-xs text-red-700">
                    {error}
                  </div>
                )}

                <button
                  disabled={loading}
                  aria-busy={loading}
                  className="inline-flex items-center gap-2 rounded-full bg-gradient-to-r from-violet-600 via-fuchsia-500 to-pink-500 px-5 py-2.5 text-sm font-semibold text-white shadow hover:shadow-lg transition"
                >
                  {loading ? (
                    <>
                      <ArrowPathIcon className="h-4 w-4 animate-spin" />
                      กำลังเปรียบเทียบ...
                    </>
                  ) : (
                    <>
                      เริ่มเปรียบเทียบ
                      <ArrowRightIcon className="h-4 w-4" />
                    </>
                  )}
                </button>
              </form>
            </section>

            {/* RIGHT */}
            <section className="space-y-4">
              {loading && !result && (
                <div className="animate-pulse rounded-2xl bg-white p-4 space-y-3">
                  <div className="h-4 bg-slate-200 rounded w-1/3" />
                  <div className="h-3 bg-slate-200 rounded" />
                  <div className="h-24 bg-slate-100 rounded-xl" />
                </div>
              )}

              {result && (
                <div className="rounded-2xl bg-white p-4 space-y-4 border">
                  <div className="flex justify-between items-center">
                    <h2 className="text-sm font-semibold text-slate-900">
                      ผลการเปรียบเทียบ
                    </h2>
                    <span
                      className={`px-3 py-0.5 rounded-full text-xs font-semibold border ${riskColor(
                        result.risk_level
                      )}`}
                    >
                      {result.risk_level}
                    </span>
                  </div>

                  <div className="text-xs text-slate-700 whitespace-pre-wrap leading-relaxed">
                    {result.summary_text}
                  </div>
                </div>
              )}
            </section>
          </div>

          {result && changes.length > 0 && (
            <div className="border-t bg-slate-50 p-6">
              <div className="flex gap-1 mb-3">
                {["ALL", "ADDED", "REMOVED", "MODIFIED"].map((t) => (
                  <button
                    key={t}
                    onClick={() => setFilterType(t as any)}
                    className="px-3 py-1 rounded-full border text-xs hover:shadow-sm transition"
                  >
                    {t}
                  </button>
                ))}
              </div>

              <div className="space-y-3 text-slate-800 text-xs">
                {filteredChanges.map((c, i) => (
                  <div
                    key={i}
                    className="rounded-xl bg-white border p-3 space-y-2"
                  >
                    <div className="flex justify-between">
                      {renderChangeBadge(c.change_type)}
                      {riskBadgeForChange(c.risk_level)}
                    </div>

                    <div className="grid md:grid-cols-2 gap-2">
                      <p className="bg-rose-50 border border-rose-200 px-2 py-1 rounded leading-relaxed line-through">
                        {truncate(c.old_text)}
                      </p>
                      <p className="bg-emerald-50 border border-emerald-200 px-2 py-1 rounded leading-relaxed">
                        {truncate(c.new_text)}
                      </p>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          )}
        </div>
      </div>
    </main>
  );
}
